
/*back ground colour*/
body{
  background-color: #fbfbf5;
}
  /*  HEADER SECTION */
/*Main header for simplyfying*/
.my-one h1{
  color: #7F7483;
  font-family: open Sans,sans-serif;
  font-weight: normal;
  padding-left: 4em;
  margin-top: 1px;
  text-align: center;
}

/*Main header for "g" colour */
.for-g{
  color: #FFCD00;
  font-family: cursive;
  font-style: normal;
  font-size: 1.2em;
}

/*Main header for burger menu */
.my-two{
  text-align:left;
}

                        /*INNER SECTION*/
/*class inner*/
.inner {
  margin-top: calc(50% -350px) !important;
  margin-bottom: 30em  !important;
}
  /*Inner-middle part area my three -we deal in */
  .my-three {
  white-space: normal;
  }
  /*Inner-middle part area my three we deal in */
  .wdi{
    font-size:6.5em;
    color: #7F7483;
    font-family: sans-serif;
    padding-left: 0.5em;
    line-height: 4em;
    }
    /*Inner-middle part 2 area class spliting 3parts/columns three -design */
  .bo-txt {
      margin-top:4.5em;

    }
/*Inner-middle part 2 area class spliting 3parts/columns three -design */
    .sc-vxr{
      display: grid;
      grid-auto-flow: dense;
      padding-left: 26em;
    }
    /*Inner-middle part 2 area class spliting 3parts/columns  -design */
    .dig{
      font-size: 4em;
      color: #FFCD00;
      padding-right: 0em;
    }


            /*FOOOTER SECTION                */
    /*footer part1 area class spliting 3parts/columns mail part */
    .ff1 {
      display: block;
    }
    .ff1 {
      color: #7F7483;
      font-family: open sans, sans-serif;
      font-size: 1.2em;
      margin-bottom: 1em;
    }
    .for-hi-g{
      color: #FFCD00;
      font-family: cursive;
      font-style: normal;
      font-size: 1.3em;
    }

    /*footer part2 area class spliting 3parts/columns "Built in Los Angeles, California."" part2 */
    .ff2 {
      display: block;
    }
    .ff2 {
      text-align: center;
      color: #7F7483;
      font-family: open sans, sans-serif;
      font-size: 1.2em;
      margin-bottom: 1em;
    }
    /*footer part3 area class spliting 3parts/columns "contact- +1 3238251687 " part3 */
    .ff3 {
      display: block;
    }
    .ff3 {
      text-align: right;
      color: #7F7483;
      font-family: open sans, sans-serif;
      font-size: 1.2em;
      margin-bottom: 1em;
    }


/* media queries for all properties for iphone5 for pixel 320-568px*/
@media only screen
and (min-width : 320px)
and (max-width : 568px){
