/*Main header for simplyfying*/
  .my-one{
    width: 80%;
    float: left;
    padding-left: 0em;
  }

/*Main header for simplyfying*/
.my-one h1{
color: #7F7483;
font-family: open Sans,sans-serif;
font-weight:normal;
padding-left:0em;
float:left;
margin-top:0.5em;
font-size: 3.5em;
}

/*Main header for "g" colour */
.for-g{
  color: #FFCD00;
  font-family: cursive;
  font-size: 1em;
}
/*Main header for burger menu */
.my-two{
  width: 20%;
  float: right;
  padding-right: 0em;
